
    import {
        useRoute,
        useRouter
    } from "vue-router";
    import {
        ref,
        reactive,
        watch
    } from "vue";
    import {
        addListener,
        Project,
        getProjects,
        getAccount
    } from "../api/contract";
    
    const headers = [{
            title: 'Project Title',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: 'Project Description',
            dataIndex: 'description',
            key: 'description'
        },
        {
            title: 'Project End Time',
            dataIndex: 'endTime',
            key: 'endTime'
        },
        {
            title: 'Current Donations (ETH)',
            dataIndex: 'amountFunded',
            key: 'amountFunded'
        },
        {
            title: 'Expense Count (ETH)',
            dataIndex: 'expenseCount',
            key: 'expenseCount',
        },
        {
            title: "Detail",
            dataIndex: 'detail',
            key: 'detail'
        }
    ]
    
    export default {
        name: "Projects",
        setup() {
            const router = useRouter();
            const route = useRoute();
            // const pageTitle = ref('All Projects')
            // const pageUpdate = () => {
            //     console.log("updating mine", route.query.mine);
            //     mine.value = route.query.mine === 'true';
            //     if (mine.value === true) {
            //         pageTitle.value = 'My Projects';
            //     }
            // }
            // pageUpdate();
    
            const createProject = (() => {
                router.push({
                    name: "NewProject"
                })
            });
            const openProjectDetail = ((index: number) => {
                router.push({
                    name: "Project",
                    params: {
                        id: index
                    }
                })
            });
    
            const state = reactive < {
                loading: boolean,
                data: Project[],
                account: string
            } > ({
                loading: true,
                data: [],
                account: '',
            });
    
            async function fetchData() {
                state.loading = true;
                try {
                    state.account = await getAccount();
                    state.data = await getProjects();
                    state.loading = false;    
                } catch (e) {
                    console.log(e);
                }
            }
            addListener(fetchData);
            fetchData();

            const showMyProject = (project: Project) => {
                const route = useRoute();
                if (route.query.mine === 'true') {
                    return project.founder === state.account;
                }
                return true;
            }

            const pageTitle = () => {
                const route = useRoute();
                if (route.query.mine === 'true') {
                    return "My Projects"
                }
                return "All Projects"
            }

            return {
                createProject,
                openProjectDetail,
                state,
                headers,
                pageTitle,
                showMyProject
            };
        }
    }
